<template>
    <div class="topUp">
        <div class="row">
            <div class="col-md-7">
                <div class="card">
                    <div class="card-body">
                        <h5 class="mb-3">
                            {{ $trans('Lịch sử nạp tiền') }}
                        </h5>
                        <slot v-if="transactions && transactions.data">
                            <div class="table-responsive">
                                <div class="list-info mb-3">
                                    <small>{{ $trans('Kết quả') }}: <strong>{{ transactions.data.length }}</strong>
                                        {{ $trans('của') }} <strong>{{ transactions.total }}</strong></small>
                                </div>
                                <table class="table table-striped">
                                    <thead>
                                    <tr>
                                        <th>{{ $trans('ID') }}</th>
                                        <th>{{ $trans('Số tiền') }}</th>
                                        <th>{{ $trans('Trạng thái') }}</th>
                                        <th>{{ $trans('Ngày') }}</th>
                                        <th>{{ $trans('Ghi chú') }}</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr v-for="(transaction,index) in transactions.data" v-bind:key="index">
                                        <td>{{ transaction.id }}</td>
                                        <td>
                                            <vue-numeric
                                                    currency-symbol-position="suffix"
                                                    currency="VND"
                                                    v-bind:value="transaction.deposit"
                                                    v-bind:placeholder="'0 VND'"
                                                    v-bind:read-only="true"
                                                    v-bind:precision="0">
                                            </vue-numeric>
                                        </td>
                                        <td>
                                          <span v-if="transaction.status=='success'" class=" badge badge-success badge-pill">
                                            {{ $trans('Thành công') }}
                                          </span>
                                                                    <span v-if="transaction.status=='waiting'"
                                                                          class=" badge badge-warning badge-pill">
                                            {{ $trans('Chờ xử lý') }}
                                          </span>
                                                                    <span v-if="transaction.status=='fail'"
                                                                          class=" badge badge-danger badge-pill">
                                            {{ $trans('Từ chối') }}
                                          </span>
                                        </td>
                                        <td>{{ formatDate(transaction.created_at) }}</td>
                                        <td>{{ transaction.desc }}</td>
                                    </tr>
                                    </tbody>
                                </table>
                            </div>
                            <paginate v-if="transactions && transactions.data && transactions.last_page"
                                      :page-count="transactions.last_page"
                                      :click-handler="paginate"
                                      :prev-text="$trans('Trước')"
                                      :next-text="$trans('Sau')"
                                      :page-class="'page-item'"
                                      :prev-class="'page-item'"
                                      :next-class="'page-item'"
                                      :page-link-class="'page-link'"
                                      :prev-link-class="'page-link'"
                                      :next-link-class="'page-link'"
                                      :container-class="'pagination'">
                            </paginate>
                        </slot>
                        <slot v-else>
                            <div v-if="alerts" class="alert" v-bind:class="'alert-'+alerts.type">
                                {{ alerts.message }}
                            </div>
                        </slot>
                    </div>
                </div>
            </div>
            <div class="col-md-5">
                <div class="card">
                    <div class="card-body">
                        <h5 class="mb-3">
                            {{ $trans('Nạp tiền') }}
                        </h5>
                        <small>{{ $trans('Các trường có dấu (*) là bắt buộc.') }}</small>
                        <hr>
                        <div v-if="alert" class="alert" v-bind:class="'alert-'+alert.type">
                            {{ alert.message }}
                        </div>
                        <div class="form-group">
                            <label>{{ $trans('Số tiền') }}(*)</label>
                            <vue-numeric class="form-control"
                                         currency-symbol-position="suffix"
                                         currency="VND"
                                         v-model="addFields.deposit"
                                         v-bind:placeholder="'0 VND'"
                                         v-bind:read-only="false"
                                         v-bind:precision="0">
                            </vue-numeric>
                            <slot v-if="errors && errors['deposit']">
                                <div class="text-danger" v-for="error in errors['deposit']"
                                     v-bind:key="error">
                                    <small>{{ error }}</small>
                                </div>
                            </slot>
                        </div>
                        <button @click="store" class="btn btn-primary m-t-20">
                            <div v-if="process" class="spinner-border spinner-border-sm text-secondary"></div>
                            {{ $trans('Nạp tiền') }}
                        </button>
                        <hr>
                        <div class="bank-info d-flex justify-content-between">
                            <div class="info">
                                <div>
                                    <div class="title__naptien">Phương thức nạp tiền:</div>
                                    <div class="content__naptien">Sau khi nhập số tiền và xác nhận</div>
                                    <div class="content__naptien">Vui lòng chuyển khoản vào số tài khoản bên dưới</div>
                                    <div class="content__naptien"><span class="content__naptien">Theo nội dung:</span>
                                        <span
                                                class="content__naptien-2">Tên đăng nhập</span></div>
                                    <div class="content__naptien" style="margin-bottom: 20px;">Ví điện tử hệ thống sẽ tự
                                        động cập nhật
                                    </div>
                                </div>
                                <div class="precautions" style="font-size: 14px;font-weight: 800">
                                    <div class="stk_deposit"><label class="title">Số Tài Khoản: </label>{{ banks.stk }}
                                    </div>
                                    <div class="stk_deposit"><label class="title">Tên Tài Khoản : </label>{{
                                        banks.ctk
                                        }}
                                    </div>
                                    <div class="stk_deposit"><label class="title">Tên Ngân hàng: </label>
                                        {{ banks.name_bank }}
                                    </div>
                                </div>
                            </div>
                            <div class="bank-qrcode">
                                <img :src="banks.qrcode" alt="">
                            </div>
                        </div>
                        <div class="precautions"><h4>Ghi chú:</h4>
                            <ol>
                                <li>Cùng một tài khoản / gia đình / địa chỉ hộ gia đình / số điện thoại / địa chỉ IP /
                                    máy tính
                                    dùng chung / môi trường mạng được coi là cùng một thành viên. Nếu nhiều tài khoản và
                                    cùng
                                    một IP được hỏi và tài khoản không rõ ràng, tất cả chúng sẽ bị coi là gian lận tạm
                                    ngưng tài
                                    khoản.
                                </li>
                                <li> Đảm bảo xác nhận số tài khoản. Nếu số tài khoản sai, công ty chúng tôi không thể
                                    chịu trách
                                    nhiệm. Sau khi thành viên tham gia lần đầu tiên sẽ bị ràng buộc vĩnh viễn và không
                                    được thay
                                    đổi tùy ý.
                                </li>
                                <li> Bộ phận kiểm soát rủi ro của công ty sẽ xem xét lại, vui lòng không vi phạm quy
                                    định.
                                </li>
                                <li> Công ty có quyền xem xét tài khoản thành viên hoặc chấm dứt vĩnh viễn các dịch vụ
                                    của thành
                                    viên mà không cần thông báo trước.
                                </li>
                                <li> Nếu phản hồi chậm do các yếu tố internet, hãy kiên nhẫn.</li>
                                <li> Các hành động quá thường xuyên sẽ được hệ thống tự động lọc ra.</li>
                            </ol>
                        </div>
                    </div>

                </div>
            </div>
        </div>

    </div>
</template>

<script>
import axios from "axios";
import moment from "moment";

export default {
    name: "TopUp",
    data: function () {
        return {
            addForm: false,
            alert: null,
            alerts: null,
            process: false,
            errors: {},
            addFields: {
                deposit: ''
            },
            transactions: null,
            transaction: null,
            filterQuery: {
                page: 1,
                from: null,
                to: new Date(),
                keyword: '',
                status: 'all',
            },
            showDetail: false,
            formFilter: false,
            process_loading: false,
            export_data: null,
            process_export_loading: false,
        }
    },
    mounted() {
        var today = new Date();
        this.filterQuery.from = new Date(today.getFullYear(), today.getMonth(), 1)
    },
    methods: {
        formatDate(date) {
            return moment(date).format("DD/MM/YYYY, HH:mm:ss")
        },
        filter: function () {
            this.filterQuery.page = 1;
            this.index()
        },
        paginate: function (pageNum) {
            this.filterQuery.page = pageNum;
            this.index();
        },
        index: function () {
            this.process_loading = true
            axios({
                url: this.$root.$data.api_url + '/api/deposit',
                method: 'GET'
            })
                .then(resp => {
                    if (resp.data.error_code === 0) {
                        this.transactions = resp.data.results;
                    } else {
                        this.alerts = {
                            type: 'warning',
                            message: resp.data.message
                        }
                        this.errors = resp.data.errors || {};
                    }
                    this.process_loading = false
                })
                .catch(err => {
                    console.log(err)
                    this.process_loading = false
                })
        },
        store: function () {
            if (this.process == false) {
                this.process = true;
                this.errors = {}
                this.alert = null
                let data = this.addFields
                if(this.addFields.deposit < 10000){
                    this.alert = {
                        type: 'danger',
                        message: 'Số tiền nạp không được nhỏ hơn 10,000VND'
                    }
                    this.process = false;
                    return false;
                }
                axios({url: this.$root.$data.api_url + '/api/deposit', data: data, method: 'POST'})
                    .then(resp => {
                        if (resp.data.error_code === 0) {
                            this.index();
                            this.alert = {
                                type: 'success',
                                message: 'Bạn đã yêu cầu nạp thành công.'
                            }
                            this.addFields.deposit = ''
                        } else {
                            this.alert = {
                                type: 'warning',
                                message: resp.data.message
                            }
                            this.errors = resp.data.errors || {};
                        }
                        this.process = false;
                    })
                    .catch(err => {
                        console.log(err)
                        this.process = false;
                    })
            }
        },

    },
    created() {
        this.index()
    },
    computed: {
        banks: function () {
            return this.$root.global_settings
        },
    }
}
</script>

<style scoped>
.content__naptien-2 {
    color: red;
}

.bank-qrcode {
    width: 30%;
}
</style>
